<template>
  <div>
    <v-header>
      <div slot="right" class="h-right" v-if="service && isLogin && !isself">
        <span :class="['img',service.isFavorite ? 'star-fill' : 'star']" @click="addFavorite"></span>
      </div>
    </v-header>
    <v-content v-show="service" :type="'load-lazy'">
      <div class="user-intro" style="background-image:url({{service.category | img-host}})">
        <div class="intro">
          <span class="img user-img" style="background-image:url({{userInfo.icon | img-host}})"
                v-if="userInfo.icon"></span>
          <span :class="['img','user-img',userInfo.sex == 1 ? 'sir' : 'lady']" v-else></span>

          <div class="info">
            <div class="info-1 clear-fix">
              <p class="username">{{userInfo.nickName}}</p>
              <span :class="['img','sex',userInfo.sex == 1 ? 'sir' : 'lady']"></span>
              <p class="age opa-8">{{userInfo.age}}</p>
              <span class="img major" v-if="service.isMajor"></span>
            </div>
            <div class="info-2 clear-fix pd-t5">
              <p class="edu"><span class="img"></span><span class="opa-7">{{userInfo.edu}}</span></p>
              <p class="location mg-l10"><span class="img"></span><span
                      class="opa-7">{{userInfo.area | area-filter}}</span></p>
              <p class="orders mg-l10"><span class="img"></span><span class="opa-7">{{service.orders}}单</span></p>
            </div>
            <div class="info-3 clear-fix">
              <p class="rate">
                <span class="img" v-for="(index,star) in stars"
                      style="background-position:-{{0.46 * star.rate}}rem 0;left:{{index*0.25}}rem;"></span>
                <span class="opa-7">{{userInfo.rate}}</span>
              </p>
            </div>
          </div>
        </div>
      </div>
      <div :class="['user-detail','service-box',haveBought == true ? '':'colorChange',haveBought == true ? '':'imgChange']">
        <p class="title color-3 font-16"><span class="img"></span>实名信息</p>
        <div class="section">
          <p class="real-name"><span class="img"></span>{{userInfo.realName}}</p>
          <p class="phone" v-if="!haveBought"><span class="img"></span>{{userInfo.phone}}</p>
          <p class="phone" v-else><span class="img"></span>
            <a href="tel:{{userInfo.phone}}" class="color-6">{{userInfo.phone}}</a></p>
          <p class="qq" v-if="userInfo.qq"><span class="img"></span>{{userInfo.qq}}</p>
          <p class="wx" v-if="userInfo.wx"><span class="img"></span>{{userInfo.wx}}</p>
          <p class="email"><span class="img"></span>{{userInfo.email}}</p>
        </div>
      </div>
      <div class="service-info service-box">
        <p class="title color-3 font-16"><span class="img"></span>服务信息</p>
        <div class="section">
          <p class="service-name ellipsis" style="max-width: 70%;">{{service.name}}</p>
          <span class="price">{{service.price | hour-price}}</span>
          <p class="matter mg-t5">服务范围<span class="mg-l10 color-6">{{service.workArea | area-filter}}</span></p>
          <p class="matter">
            <span class="left">服务日期</span>
            <span class="mg-l10 color-6 left">

              <span v-if="service.timeless">长期有效</span>
              <span v-else>{{service.startTime}}-{{service.endTime}}</span>

              ({{service.workSplit | week-format}})</span>
            <span class="img calendar left" @click="toCalendar"></span>
          </p>
          <p class="matter">服务时间<span class="mg-l10 color-6">{{service.workTime}}</span></p>
          <p class="matter">服务地址<span class="mg-l10 color-6">{{service.address}}</span></p>
        </div>
        <div class="section mg-r10">
          <p class="matter">技能介绍</p>
          <p style="line-height:1.8;">{{service.intro}}</p>
        </div>
        <div class="section mg-r10" v-if="service.description.length > 0">
          <p class="matter">服务说明</p>
          <p style="line-height:1.8;">{{service.description}}</p>
        </div>
      </div>
      <div class="service-box user-certificate" v-if="certificates.length > 0">
        <p class="title color-3 font-16"><span class="img"></span>技能证书</p>
        <div :class="['section','tran-all-3',item.showAll?'show':'']" v-for="item in certificates" @click="showAll(item)">
          <div style="margin-left:.6rem;">
            <div class="color-3" style="font-weight: bolder;">
              <p class="left">{{item.name}}</p>
              <span class="img verified left"></span>
            </div>
            <p class="font-12 clear-fix" style="line-height:1.8;">{{item.code}}</p>
            <div v-show="item.showAll" class="font-12" style="line-height:1.8;">
              <p>有效期限：{{item.deadLine}}</p>
              <p>颁发机构：{{item.org}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="service-box user-skill-show" v-if="skillShow.length > 0">
        <p class="title color-3 font-16"><span class="img"></span>技能秀</p>
        <div class="section min-view">
          <div style="width:{{(picSize+5)*skillShow.length}}px;overflow: auto;">
            <span v-for="item in skillShow" class="img lazyload" @click="fullView($index)"
             data-original="{{item.imgPath | img-host}}"
             style="width:{{picSize}}px;height:{{picSize}}px;"></span>
          </div>
        </div>
      </div>
      <div class="user-skills service-box" v-if="skills.length > 0">
        <p class="title color-3 font-16"><span class="img"></span>其他技能</p>
        <div class="section">
          <span class="skill" v-for="skill in skills | limitBy 3">{{skill.name}}</span>
        </div>
      </div>
    </v-content>
    <v-content v-show="!service">
      <div class="none_bg" v-if="!service && loaded">
        <span class="img bg06"></span>
        <p class="remark" style="text-align:left;">
          您访问的匠人服务页面不存在或已过期,请返回
          <a class="color-link" v-link="{name:'service-list'}">匠人列表</a>
          或<a class="color-link" v-link="{name:'home'}">首页</a>重新搜索。
        </p>
      </div>
    </v-content>
    <v-footer-btn class="opa-9" v-if="service">
      <input class="ok max input" type="button" value="查看匠人完整信息" v-if="!haveBought && !isself"
             @click="viewInfo"/>
      <input style="background-color: #ffa800;" :class="['max','input',isself?'nk':'ok']" type="button" value="点匠" v-if="haveBought" @click="isSpot"/>
    </v-footer-btn>
    <v-pic-view></v-pic-view>
  </div>
</template>
<script>
  import VHeader from '../components/Header'
  import VContent from '../components/Content'
  import VFooterBtn from '../components/FooterBtn'
  import VPicView from '../components/PicView'
  import {lt, url} from '../const'
  import {toast} from '../assets/js/toast'
  import $ from 'jquery'
  import Vue from 'vue'
  require('../assets/js/lazyload')

  export default{
    route: {
      data({from, to}){
        this.loaded = false;
        this.review = false;
        this.params = to.params;
        this.service = null;
        this.certificates = [];
        this.$broadcast('login-check');
        this.$broadcast('reset-pic-view');
        toast.loading();
        return this.$http.get(url.serviceDetail, {id: to.params.id}).then(
                ({data:{data, success, resultMsg}}) => {
                  this.loaded = true;
                  if (success) {
                    if (data) {
                      this.skills = data.skills;
                      this.service = data.service;
                      this.userInfo = data.userInfo;
                      this.haveBought = data.haveBought;
                      this.isself = data.isself;
                      this.skillShow = data.skillsShow;
                      let certificates = data.certificates;
                      for(let i in certificates){
                        certificates[i].showAll = false;
                        this.certificates.push(certificates[i]);
                      }
                      if (data.userInfo.qq && data.userInfo.wx) {
                        this.userDetails = 3;
                      } else {
                        this.userDetails = 2;
                      }
                      let stars = [{rate: 1}, {rate: 1}, {rate: 1}, {rate: 1}, {rate: 1}];
                      let rate = parseFloat(this.userInfo.rate);
                      for (let i = 0; i < rate; i++) {
                        stars[i].rate = (rate - i) >= 1 ? 0 : (rate - i) == 0 ? 1 : 0.5;
                      }
                      this.stars = stars;
                      this.lazyload();
                    }
   /*                 if(this.haveBought) {
                      $(".user-detail .section p").css("color","red");
                      $(".user-detail .section .img").css("background-image","url(../assets/img/icon/i_msg.png)");
                    }else {
                      $(".user-detail .section p").css("color","yellow");
                      $(".user-detail .section .img").css("background-image","url(../assets/img/icon/i_msg_dim.png)");
                    }*/
                  } else {
                    toast.alert(resultMsg);
                  }
                })
                .catch(function (response) {
                  this.loaded = true;
                })
      }
    },
    ready(){
      $('.none_bg').css('height', $(window).height() * 0.9 + "px");
      this.$broadcast('login-check');
      let size = $(window).width()-20-5*3;
      this.picSize = size / 4;
    },
    data(){
      return {
        isLogin: false,
        userInfo: null,
        skills: [],
        certificates: [],
        skillShow:[],
        service: null,
        haveBought: false,
        userDetails: 2,
        params: {},
        stars: [],
        loaded: false,
        isself: false,//是否是本人
        picSize:0,
        review: false
      }
    },
    methods: {
      lazyload(){
        this.$nextTick(function(){
          $(".lazyload").lazyload({
            effect: "fadeIn",
            threshold: 5,
            container:$('.section.min-view')
          });
        });
      },
      fullView(index){
        let list = this.skillShow;
        let items = [];
        let link = '';
        for(let i=0;i<list.length;i++){
          link = list[i].imgPath;
          link = link.split('.').join('big.');
          items.push({
            id:list[i].id,
            link:link
          });
        }
        let data = {items:items,index:index,review:this.review};
        this.$broadcast('pic-view',data);
        this.review = true;
      },
      showAll(item){
        item.showAll = !item.showAll;
      },
      toCalendar(){
        let link = "#/calendar/his/";
        let sex = this.userInfo.sex;
        if (sex != 1) {
          link = "#/calendar/her/";
        }
        window.location.href = link + this.service.id;
      },
      viewInfo(){
        if (!this.isLogin) {
          let vm = this;
          toast.conform('亲，您应该先登录哟~', null, true, function () {
            window.location.href = '#/login?from=' + vm.$route.path;
          }, {do: '再逛逛', cancel: '我要登录'});
          return;
        }
        this.$http.get(url.secretInfo, {id: this.params.id, userId: this.userInfo.userId}).then(
                function ({data:{data, success, resultMsg}}) {
                  if (success) {
                    $('.content').animate({scrollTop: '0px'}, 100);
                    this.userInfo.email = data.email;
                    this.userInfo.realName = data.realName;
                    this.userInfo.qq = data.qq;
                    this.userInfo.wx = data.wx;
                    this.userInfo.phone = data.phone;
                    this.haveBought = true;
                  } else {
                    toast.alert(resultMsg);
                  }
                }
        )
      },
      addFavorite(){
        if (!this.isLogin) {
          window.location.href = "#/login?from=/service/" + this.params.id;
          return;
        }
        this.$http.get(url.addFavorite, {jobId: this.params.id}).then(
                function ({data}) {
                  if (data.success) {
                    let isFavorite = data.data.isFavorite;
                    Vue.set(this.service, 'isFavorite', isFavorite);
                    if (isFavorite) {
                      toast.alert('已收藏');
                    } else {
                      toast.alert('已取消');
                    }
                  } else {
                    toast.alert(data.resultMsg);
                  }
                }
        )

      },
      isSpot(){
        let vm = this;
        if (vm.isself) {
          return;
        }

        this.$http.get(url.isSpotArtisan).then(
                function ({data:{success, data, code}}) {
                  if (!success) {
                    if (code == 102) {
                      setTimeout(function () {
                        toast.conform('请先完善个人信息，再发布', null, true, function () {
                          window.location.href = '#/user/info?from=' + vm.$route.path;
                        }, {do: '再逛逛', cancel: '去完善'});
                      }, 100);
                    } else if (code == 103) {
                      setTimeout(function () {
                        toast.conform('请先实名认证，再发布', null, true, function () {
                          window.location.href = '#/identify/real/step1?from=' + vm.$route.path;
                        }, {do: '再逛逛', cancel: '去认证'});
                      }, 100);
                    }
                  } else {
                    window.location.href = '#/order/add/' + vm.service.id;
                  }
                });
      }
    },
    events: {
      'is-login': function (isLogin) {
        this.isLogin = isLogin;
      }
    },
    components: {VHeader, VContent,VPicView, VFooterBtn}
  }
</script>
