<template>
    <div :class="['pic-swiper tran-all-3 z-index-1',{active:isView},{'alone':standalone}]">
        <div class="my-header z-index-1">
            <div class="warp">
                <div class="h-left" @click="closeView">
                    <span :class="['img back',{'alone':standalone}]" v-show="isView"></span>
                </div>
            </div>
        </div>
        <div class="swiper-container" style="height:100%;">
            <div class="swiper-wrapper">
                <div class="swiper-slide z-index-max pic" v-for="item in items">
                    <div data-background="{{item.link | img-host}}" class="swiper-lazy"
                         style="background-size: 100%;background-repeat: no-repeat;background-position: center;height:100%;width:100%;"></div>
                    <div class="swiper-lazy-preloader"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery'
    import {DJUtils} from '../assets/js/common'
    import Swiper from '../assets/js/swiper-3.4.0.min';

    export default{
        ready(){
            this.$on('pic-view', function (data) {
                if (data.review && data.review == true) {
                    this.review = true;
                } else {
                    this.review = false;
                }
                if (this.items.length == 0) {
                    this.items = data.items;
                } else {
                    let thisOj = JSON.stringify(this.items);
                    let newOj = JSON.stringify(data.items);
                    if (thisOj != newOj) {
                        this.items = data.items;
                    }
                }
                this.isView = true;
                this.$nextTick(function () {
                    this.init(data.index);
                });
            });
            this.$on('reset-pic-view', function () {
                this.isView = false;
                this.items = [];
            });
            this.standalone = DJUtils.isStandalone();
        }
        ,
        data()
        {
            return {
                items: [],
                isView: false,
                mySwiper: {},
                review: false,
                standalone: false
            }
        },
        methods: {
            init(index){
                let vm = this;
                if (vm.review) {
                    vm.mySwiper.slideTo(index, 0, false);
                } else {
                    setTimeout(function () {
                        vm.mySwiper = new Swiper('.pic-swiper .swiper-container', {
                            lazyLoading: true,
                            loop: false,
                            observer: true,//修改swiper自己或子元素时，自动初始化swiper
                            observeParents: true//修改swiper的父元素时，自动初始化swiper
                        });
                        if (index > 0) {
                            vm.mySwiper.slideTo(index, 0, false);
                        }
                    }, 300);
                }
            },
            closeView(){
                this.isView = false;
            }
        }
    }


</script>
<style scoped>
    .pic-swiper {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 0;
        background-color: #000;
    }

    .pic-swiper.alone{
        top: .4rem;
    }

    .pic-swiper.active {
        width: 100%;
    }

    .pic-swiper .pic {
        vertical-align: middle;
    }

    .pic-swiper .pic img {
        width: 100%;
        vertical-align: middle;
    }

    .my-header {
        position: absolute;
    }

    .my-header .warp {
        background-color: #000;
    }

    .back.alone {
        margin-top: .2rem;
    }
</style>



